body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: enter;
          align-items: enter;
  height: 100vh;
  width: 100%;
  margin-top: 100px;
}

.calc-wrapper{
  width: 400px;
  height: 600px;
}

.row{
  display: -webkit-flex;
  display: flex;
  width: 100%;
}
.Button{
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
	height: 4em;
	-webkit-justify-content: center;
	        justify-content: center;
	font-weight: lighter;
	font-size: 1.4em;
	-webkit-align-items: center;
	        align-items: center;
	background-color: #e0e1e6;
	color: #888;
	outline: 1px solid #888;
}

.operator{
	background-color: orange;
	color: white;
}
.Input{
	display: -webkit-flex;
	display: flex;
	height: 4em;
	-webkit-flex: 1 1;
	        flex: 1 1;
	background-color: #000;
	color: white;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	padding: 0.5em;
	font-weight: bold;
	font-size: 1.4em;
	-webkit-align-items: center;
	        align-items: center; 
}
.ClearButton{
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
	height: 4em;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	outline: 1px solid #888;
	background-color: grey; 
}	
